<template>
  <div class="infoTable">
            <table class="table table-bordered">
              <thead>
              <tr class="grayBg">
                <th class="w30"></th>
                <th class="w15"></th>
                <th class="w15"></th>
                <th class="w40"></th>
              </tr>
              </thead>
            </table>
            <div v-for='item in reDataList'>
              <table class="table table-bordered mt10">
              <tbody>
              <tr class="grayBg">
                <td colspan="4">
                  <div class="col-xs-12">
                    <div class="col-xs-3"><p>{{timeNormal(item.tradecreatetime)}}</p></div>
                    <div class="col-xs-3"><p>{{item.tradeid}}</p></div>
                    <div class="col-xs-6"><p>{{item.sellershopname}}</p></div>
                  </div>
                </td>
              </tr>
              <tr>
                <td class="w30"><p class="productname" v-for='it in item.productList'>{{it.productname}}</p></td>
                <td class="w15">{{item.actualfee}}</td>
                <td class="w15">{{item.tradetext}}</td>
                <td class="w40">
                  <p>{{item.delivername}}</p>
                  <p>{{item.delivermobilephone}}</p>
                  <p>{{item.deliverfulladdress}}</p>
                </td>
              </tr>
              </tbody>
            </table>
            </div>
            <div class="noData">
                {{errorInfo}}
            </div>
            <page :page-info="pageInfo" :total="total"></page>
          </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import page from '../basic/page/page.vue'
var pagesize = 10;
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': '','pageNum': 1, 'total': 0,}
        }
        ,computed: {
            //
        'pageInfo': function () {
            return {'pageSize': pagesize, 'currentpage': this.pageNum}
        }
           ,'qParams': function () {
            let _offset = this.cptOffset(this.pageNum, pagesize)
             return {'id': this.id,'offset': _offset, 'pageSize': pagesize}
           }
           ,'url': function () { //
            return action.rootPath + action.interface.taobaoOrder
           }
       }
       ,components: {page}
       ,mixins: [mixins]
       ,created () {
        this.doQuery();
        //go-page
        eventHub.$on('go-page', this.goPage);
       }
       ,destroyed (){
        eventHub.$off('go-page', this.goPage);
       }

  }
</script>



