<script>
/*
 * 
 */
import action from '../../action/interface.js' 
import mixins from '../mixin.js'
import dict from '../../js/dict.js'


var queryProd = action.rootPath + action.interface.queryById
    export default {
        props: ['modalProps']
        ,data () {
            return {'reDataList':[],'dict':dict}
        }
        ,components: {}

        ,created () {
            let vm = this
            let id = vm.modalProps.id;
            if(id){
                //
                vm.send(queryProd, {'id': vm.modalProps.id}, function (res) {
                if (res.errorcode == 0) {
                    vm.reDataList = res.result
                }
                })                   
            }
            
        }
        ,mounted () {
            eventHub.$on('do-save', this.doSave)
            eventHub.$on('go-back', this.goBack)
        }           
        ,methods: {
