
export default {
    methods: {
        // 
        toPercent(value) {
            //
            let _v = parseFloat(value)
            if (!isNaN(_v)) {
                return (_v * 100).toFixed(4);
            } else {
                return value;
            }
        }
        , percentToFloat(percent) { //20 => 0.2
            let _v = parseFloat(percent)
            if (!isNaN(_v)) {
                return _v / 100;
            } else {
                return _v;
            }
        }
        //
        //@params: cyclecycleUnit
        , showCycle(cycle, cycleUnit) {
            var showUnit = '';
            if (cycle == undefined || cycleUnit == undefined) return '';
            if (cycleUnit == 0) {
                showUnit = '';
            } else {
                showUnit = '';
            }
            return cycle + showUnit;
        }
        //
        // (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423 
        // (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18 
        , fDate(_date, fmt) {
            //
            let _d = parseInt(_date)
            if (isNaN(_d)) return _date
            let _fDate = new Date(_d)
            var o = {
                "M+": _fDate.getMonth() + 1, // 
                "d+": _fDate.getDate(), // 
                "h+": _fDate.getHours(), // 
                "m+": _fDate.getMinutes(), // 
                "s+": _fDate.getSeconds(), // 
                "q+": Math.floor((_fDate.getMonth() + 3) / 3), // 
                "S": _fDate.getMilliseconds() // 
            };
            if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (_fDate.getFullYear() + "").substr(4 - RegExp.$1.length));
            for (var k in o)
                if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
            return fmt;
        }
        //,n , s
        , fMoney(s, n) {
            n = n > 0 && n <= 20 ? n : 2;
            //S0
            if (!s) {
                return 0;
            }
            s = parseFloat((s + "").replace(/[^\d\.-]/g, "")).toFixed(n) + "";
            var l = s.split(".")[0].split("").reverse(), r = s.split(".")[1];
            var t = "";
            for (var i = 0; i < l.length; i++) {
                t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? "," : "");
            }
            return t.split("").reverse().join("") + "." + r;
        }
        //
        , rMoney(fm) {
            if (typeof (fm) == 'number') return fm;
            return parseFloat(fm.replace(/[^\d\.-]/g, ""));
        }
        //
        //return ms
        , getDateDiff(startDate, endDate) {
            try {
                var sDate = new Date(startDate),
                    eDate = new Date(endDate);
                return (eDate - sDate);
            } catch (e) { }
        }
        //,(NN)
        , fDateDiff(startDate, endDate) {
            var diff = this.getDateDiff(startDate, endDate);
            diff = parseInt(diff);
            if (typeof (diff) != 'number') return '';
            diff = parseInt(diff / (1000 * 60 * 60 * 24));
            var month = Math.floor(diff / 30),
                day = diff % 30;
            month = (month > 0) ? (month + "") : "";
            day = (day > 0) ? (day + "") : "";
            return (month + day);
        }
        //dict.js
        , getDictValue(key, obj) {
            //key
            if (typeof (obj) != 'object') return key;
            if (key === '' || key == 'undefined') return key;
            key = '' + key;
            return obj[key];
        }

        //
        , error(msg) {
            eventHub.$emit('show-alert', msg);
        }
        //get
        , send(url, params, callback, errorcall) {
            if (url == 'undefined' || url == '' || typeof (params) != 'object') return;
            //
            $('#loading').show();
            this.$http({
                'method': 'get',
                'headers': {"X-Requested-With": "XMLHttpRequest"},
                'emulateJSON': true,
                'url': url,
                'params': params
            }).then((response) => {
                //
                $('#loading').hide();
                //
                callback(response.data);
                //callback(JSON.parse(response.data));
            }, (response) => {
                $('#loading').hide();
                //
                if (typeof (errorcall) == 'function') {
                    errorcall(response);
                } else {
                    this.error('')
                }
            })
        }
        , post(url, params, callback, errorcall) { //post 
            if (url == 'undefined' || url == '' || typeof (params) != 'object') return;
            //
            $('#loading').show();
            this.$http.post(url, params,{headers: {"X-Requested-With": "XMLHttpRequest"},'emulateJSON': true,credentials: true}).then((response) => {
                //
                $('#loading').hide();
                //
                callback(response.data);
                //callback(JSON.parse(response.data));
            }, (response) => {
                $('#loading').hide();
                //
                if (typeof (errorcall) == 'function') {
                    errorcall(response);
                } else {
                    this.error('')
                }
            })
        }
        //pageNum pageSize
        , cptOffset(pageNum, pageSize) {
            let num = parseFloat(pageNum);
            let size = parseFloat(pageSize);
            if (!isNaN(num) && !isNaN(size)) {
                return (num - 1) * size;
            } else {
                return 0;
            }
        }
        /* data: pageNum, url, qParams; errorInfo() reDataList , total  --start*/
        //N
        , goPage(num) {
            //num
            num = parseInt(num)
            if (isNaN(num) || num == this.pageNum) return
            this.pageNum = num
            this.doQuery()
        }
        //
        , clickQuery() {
            //
            this.pageNum = 1;
            this.doQuery();
        }
        //
        , doQuery() {
            let vm = this
            let _params = this.qParams
            vm.post(vm.url, _params, function (res) {
                // if (res.errorcode == 0) {
                //     vm.reDataList = res.result.rows
                //     vm.total = res.result.count
                // } else if (res.errorcode == 0 && res.result.rows.length <= 0) { //
                //     vm.errorInfo = ""
                // } else { //
                //     vm.errorInfo = res.message
                // }
                if (res.errorcode == 0 && res.result) {
                    if(res.result.rows && res.result.rows.length>=0){
                        vm.reDataList = res.result.rows
                        vm.errorInfo = res.result.rows.length > 0 ? "" :""
                        if(res.result.count){
                            vm.total = res.result.count
                        }else if(res.result.total){
                            vm.total = res.result.total
                        }else{
                            vm.total = 0
                        }
                    }else{
                        vm.reDataList = res.result
                    }
                } else if (res.errorcode == 0 && !res.result ) { //
                    vm.errorInfo = ""
                } else { //
                    vm.errorInfo = res.message
                }
            })
        }
        /**
         * yy-mm-dd hh-ii-ss
         *@param{Object}time
         */
        ,timeNormal(time) {
            var now = new Date(parseInt(time));
            var yy = now.getFullYear(); //
            var mm = now.getMonth() + 1; //
            var dd = now.getDate(); //
            var hh = now.getHours(); //
            var ii = now.getMinutes(); //
            var ss = now.getSeconds(); //
            var clock = yy + "-";
            if (mm < 10) clock += "0";
            clock += mm + "-";
            if (dd < 10) clock += "0";
            clock += dd + " ";
            if (hh < 10) clock += "0";
            clock += hh + ":";
            if (ii < 10) clock += "0";
            clock += ii + ":";
            if (ss < 10) clock += "0";
            clock += ss + " ";
            return clock;
        }
        /**
         * hh-ii-ss
         *@param{Object}time
         */
        ,timeSecond(time) {
            var hh = parseInt(time/3600); //
            var ii = parseInt((time-hh*3600)/60); //
            var ss = parseInt((time-hh*3600)%60); //
            //var clock = hh + "-";
            var clock ;
            if (hh < 10) hh = "0" + hh; 
            clock = hh + ":";
            if (ii < 10) clock += "0";
            clock += ii + ":";
            if (ss < 10) clock += "0";
            clock += ss + " ";
            return clock;
        }
        /*   --end*/

    }
    , activated() {
        //go-page
        eventHub.$on('go-page', this.goPage);
        //do-query
        eventHub.$on('do-query', this.clickQuery);
        //do-refresh
        eventHub.$on('do-refresh', this.doQuery);

    }
    , deactivated() {
        //go-page
        eventHub.$off('go-page');
        //do-query
        eventHub.$off('do-query');
        //do-refresh
        eventHub.$off('do-refresh');
    }
    , beforeDestroy() {
        //
        eventHub.$off('do-save')
        eventHub.$off('go-back')
    }
};



