<template>
<div class="infoTab">
    <div class="title col-xs-12"></div>
 <div class="col-xs-12 info clearfix">
      <div class="col-xs-4">
        <p>
          <label></label>
          <span>{{reDataList.uname}}</span>
        </p>
        <p>
          <label></label>
          <span>{{reDataList.loanDay}}</span><span v-if="reDataList.termType==0"></span><span v-if="reDataList.termType==1"></span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label></label>
          <span>{{reDataList.idCard}}</span>
        </p>
        <p>
          <label></label>
          <span>{{toPercent(reDataList.dailyRate)}}</span>%
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label></label>
          <span>{{reDataList.loanMoney}}</span>
        </p>
        <p>
          <label></label>
          <span>{{getDictValue(reDataList.paymentMethod, dict.paymentMethod)}}</span>
        </p>
      </div>
      <div class="col-xs-4">
        <p>
          <label></label>
          <span>{{reDataList.productName}}</span>
        </p>
      </div>
    </div>
     </div>
</template>
<script>
import action from '../../action/interface.js'
import mixins from '../mixin.js'
import dict from '../../js/dict.js'
export default {
        props: ["id"]
        ,data () {
            return {'reDataList': '','errorInfo': '','dict': dict}
        }
        ,computed: {
           'qParams': function () {
             return {'id':this.id,'loanNo':this.$route.query.loanNo}
           }
           ,'url': function () { //
            return action.rootPath + action.interface.queryLoanInfo
           }
       }
       ,mixins: [mixins]
       ,created () {
           this.doQuery()
       }
  }
</script>


