
<style lang="scss">
    @import "./nav-tree.scss";

</style>
    <template>
    <transition name="slide-left">
        <div class="nav-tree" v-show="show">
            <div class="nav-header">
                <img/>
                <div>
                    <div class="text-weight"></div>
                    <div class="text-weight">{{uname}}</div>
                </div>            
                <a href="javascript:;" class="close" @click="toggleNav"></a>
            </div>

            <div class="tree">
                <ul class="root-menu">
                    <template v-for="(list, index) in menuList">
                        <li :class="{active: list.checked}">
                            <i :class="[list.checked ? 'icon-caret-down' : 'icon-caret-right', 'sub-control']" v-if="list.children"></i>
                            <a href="javascript:void(0);" v-if="list.type == 0" @click.stop="clickNode(list.id)">
                                <i class="icon-list"></i>
                                <span>{{list.name}}</span>
                            </a>
                            <a href="javascript:void(0);" @click="clickNode(list.id)" v-else>
                                <i class="icon-list"></i>{{list.name}}
                            </a>
                            <ul class="sub-menu" v-show="list.type == 0 && list.checked" :class="{'active': list.checked, 'checked': list.checked}">
                                <template v-for="item in list.children">
                                    <li  :class="{'blue-header': item.checked}">
                                        <a href="javascript:void(0);" @click="clickNode(item.id)">{{item.name}}</a>
                                    </li>                                    
                                </template>

                                
                            </ul>
                        </li>                        
                    </template>

                </ul>
            </div>

        </div>
    </transition>
    </template>
<script>
/*
 * 
 * api: {'toggle-nav': }
 */
import mixins from '../../mixin.js'
import action from '../../../action/interface.js'
//
    let data = {"column":{"id":45,"name":"","description":"","data":"","type":0,"children":[{"id":46,"name":"","description":"","data":"","type":0,"children":[{"id":71,"name":"","description":"","data":"/white/query","type":1},{"id":56,"name":"","description":"","data":"/customerManagement","type":1}]},{"id":52,"name":"","description":"","data":"/authManage","type":1},{"id":48,"name":"","description":"","data":"/creditManage","type":1},{"id":127,"name":"","description":"","data":"/loanManage","type":1},{"id":147,"name":"","description":"","data":"/postLoanManage","type":1},{"id":146,"name":"","description":"","data":"/delayManage","type":1},{"id":51,"name":"","description":"","data":"/contractManage","type":1},{"id":50,"name":"","description":"","data":"/prodManage","type":1},{"id":47,"name":"","description":"","data":"","type":0,"children":[{"id":73,"name":"","description":"","data":"/loanStatistics","type":1},{"id":75,"name":"","description":"","data":"/delayStatistics","type":1}]},{"id":49,"name":"","description":"","data":"","type":0,"children":[{"id":74,"name":"","description":"","data":"loginQuery","type":1},{"id":76,"name":"","description":"","data":"/loginStatistics","type":1}]},{"id":148,"name":"","description":"","data":"/logOperation","type":1}]},"userName":"pan.junlin"}

    let menuList = data.column.children

    //console.log(menuList)
    export default {
        mixins: [mixins]
        ,data () {
            return {
                'menuList': [] //
                ,'openMenu': ''  //menuListID
                ,'uname': '' //
                ,'show': true
            }
        }
        ,created () {
            //
            eventHub.$on('toggle-nav', this.toggleNav)
            // 
            let menuAction = action.rootPath + action.interface.menu
            let vm = this

            //
            // let _list = menuList
            // _list.map(function (item) {
            //     //
            //     item.checked = false
            //     //
            //     if (item.type == 0) {
            //         let _children = item.children
            //         _children.map(function (child) {
            //             child.checked = false                      
            //         })
            //     }
            // })
            // vm.menuList = _list  
            //

           vm.post(menuAction, {}, function (res) {
                //
                //res = JSON.parse(res).result;
                //res = res.result;
                console.log(res)
                //console.log(typeof(res))
                if(res.column.children){
                    let _list = res.column.children
                    let _id = 1 ,_childId = 20;

                    _list.map(function (item) {
                        //
                        _id = _id +1;
                        item.id = _id;
                        item.checked = false
                        //
                        if (item.type == 0) {
                            let _children = item.children
                            _children.map(function (child) {
                                _childId = _childId + 1;
                                child.id = _childId;   
                                child.checked = false                      
                            })
                        }
                    })
                    vm.menuList = _list   
                    vm.uname = res.userName
                    //username
                    eventHub.$emit('show-uname', res.userName)         
                }
            })
        }
        ,methods: {
            //
            toggleNav () {
                this.show = !this.show
            }
            // 
            ,clickNode (_nodeId) {
                let _list = this.menuList
                let _curItem;
                let menuStatus;
                //
                _list.map(function(item){
                    //
                    if (_nodeId == item.id) {
                        _curItem = item
                        //checked
                        if (item.type == 0) {
                            item.checked = !item.checked
                        } else {        //checked = true
                            item.checked = true
                        }
                    } else { //,
                        if (item.type == 0) {
                            let _children = item.children
                            let _clickChild = false //
                           //
                            _children.map( function(child) {
                               if (_nodeId == child.id) {
                                    _curItem = child
                                    child.checked = true
                                    _clickChild = true
                                } else {        
                                    child.checked = false
                                }
                           }) 
                            //
                            if (!_clickChild) {
                                item.checked = false
                            }
                        } else {
                            item.checked = false
                        }
                    }
                })
                //this.$set('menuList', _list)
                this.menuList = _list
                // tab
                console.log(JSON.stringify(_curItem))
                if (_curItem.type == 1) {
                // tab-nav
                    eventHub.$emit('add-tab', _curItem.data, _curItem.name)
                //change route
                    this.$router.push(_curItem.data)
                }
            }
        }
    }
</script>



